import logo from './logo.svg';
import { Container, Navbar, Nav } from 'react-bootstrap'; // 리액트부트스트랩 import
import './App.css';
import bg from './img/bg.png';  // 이미지 import
import { lazy, Suspense, createContext, useEffect, useState } from 'react';
import data from './data.js'; // data.js import
import { Routes, Route, Link, useNavigate, Outlet } from 'react-router-dom'  // 리액트라우터돔 import
// import Detail from './routes/Detail.js'; // deatil.js import
import axios from 'axios'
import $ from 'jquery';
// import Cart from './routes/Cart.js';
import { useQuery } from '@tanstack/react-query';

export let Context1 = createContext()  // state 보관함 export

// lazy 문법
const Detail = lazy(()=>import('./routes/Detail.js'));
const Cart = lazy(()=>import('./routes/Cart.js'));

function App() {

  // 재고 state
  let [재고, 재고변경] = useState([10, 11, 12]);  

  // state 선언
  let [shoes, setShoes] = useState(data);

  // 더보기 버튼 카운트
  let [count, setCount] = useState(0);

  // useNavigate 함수 선언
  let navigate = useNavigate();

  // useQuery
  let result = useQuery(['작명'], async () => {
    try {
      const response = await axios.get('https://codingapple1.github.io/userdata.json');
      return response.data || {}; // 데이터가 없는 경우 빈 객체 반환
    } catch (error) {
      console.error('데이터를 불러오는 중 에러 발생:', error);
      return {}; // 에러 발생 시 빈 객체 반환
    }
  });


  // 로드 시 로칼에 array 하나 생성
  useEffect(()=>{
    if (localStorage.getItem('watched') == null) {
      localStorage.setItem('watched', JSON.stringify([]));
    }
  }, [])


  return (
    <div className="App">

      {/* navbar */}
      <Navbar bg="light" data-bs-theme="light">
        <Container>
          <Navbar.Brand href="#home">건우쇼핑몰</Navbar.Brand>
          <Nav className="me-auto">
            {/* navigate 문법 */}
            <Nav.Link onClick={() => { navigate('/') }}>Home</Nav.Link>
            <Nav.Link onClick={() => { navigate('/cart') }}>Cart</Nav.Link>
          </Nav>
          <Nav className="ms-auto">
            { result.isLoading && '로딩중' }
            { result.error && '에러남' }
            { result.data && result.data.name }
          </Nav>
        </Container>
      </Navbar>

      {/* 페이지 이동 */}
      {/* <Link to="/">홈</Link>
      <Link to="/detail">상세페이지</Link> */}

      {/* 라우팅 */}
      {/* 로딩중일 때 로딩중임 띄우기 */}
      <Suspense fallback={<div>로딩중임</div>}>
        <Routes>

          {/* 메인화면으로 접속 시 */}
          <Route path='/' element={
            <>
              {/* main-bg */}
              <div className='main-bg' style={{ backgroundImage: `url(${bg})`}}></div>

              {/* 물건 리스트 나열 */}
              <div className='container'>
                <div className='row'>

                  {/* 반복문으로 물건 생성 */}
                  {shoes.map(function (elem, index) {
                    return <Card elem={elem} index={index} key={index}></Card>  // props 전송
                  })}

                </div>
              </div>

              {/* 상품 더 보여주는 버튼 */}
              <button onClick={() => {
                <div className='load'>로딩중입니다.</div>
                if (count == 0) {
                  axios.get('https://codingapple1.github.io/shop/data2.json')
                    .then((result) => {
                      let copy = [...shoes, ...result.data];
                      setShoes(copy);
                      $('.load').fadeOut();
                    })
                    .catch(() => {
                      console.log('실패함');
                      $('.load').fadeOut();
                    });
                }
                if (count == 1) {
                  axios.get('https://codingapple1.github.io/shop/data3.json')
                    .then((result) => {
                      let copy = [...shoes, ...result.data];
                      setShoes(copy);
                    })
                    .catch(() => {
                      console.log('실패함');
                    });
                }
                if (count == 2) {
                  alert('상품이없습니다');
                }
                setCount(count + 1);
              }}>더보기</button>

            </>
          }></Route>

          {/* detail로 접속 시 */}
          {/* url 파라미터 */}
          <Route path='/detail/:id' element={
            // context API 활용
            
              <Context1.Provider value={{재고}}>  
                <Detail shoes={shoes}></Detail>
              </Context1.Provider>
            
          }></Route>

          {/* nested routes */}
          <Route path='/about' element={<About></About>}>
            <Route path='member' element={<div>멤버임</div>}></Route>
            <Route path='location' element={<div>위치정보임</div>}></Route>
          </Route>

          {/* event로 접속 시 */}
          <Route path='/event' element={<Event></Event>}>
            <Route path='one' element={<div>첫 주문시 양배추즙 서비스</div>}></Route>
            <Route path='two' element={<div>생일기념 쿠폰받기</div>}></Route>
          </Route>

          <Route path='/cart' element={<Cart></Cart>}></Route>

          {/* 주소 잘못 입력했을 때 */}
          <Route path='*' element={<div>없는페이지요</div>}></Route>
        </Routes>
      </Suspense>

    </div>
  );
}

// Event 컴포넌트
function Event() {
  return (
    <div>
      <h4>오늘의 이벤트</h4>
      <Outlet></Outlet>
    </div>
  )
}

// About 컴포넌트
function About() {
  return (
    <div>
      <h4>회사정보임</h4>

      {/* Outlet 문법 */}
      <Outlet></Outlet>
    </div>
  )
}

// Card 컴포넌트 (props 활용)
function Card(props) {
  return (
    <div className='col-md-4' key={props.index}>
      <Link to={`/detail/${props.index}`}>
        <img src={'https://codingapple1.github.io/shop/shoes' + parseInt((props.index) + 1) + '.jpg'} width="80%"></img>
        <h4>{props.elem.title}</h4>
        <p>{props.elem.price}</p>
      </Link>
    </div>
  )
}

export default App;
