import { createSlice } from '@reduxjs/toolkit'

// user 이름 저장
let user = createSlice({
    name : 'user',
    initialState : 'kim'
})


// product 저장
let product = createSlice({
    name : 'product',
    initialState : [
        {id : 0, name : 'White and Black', count : 2},
        {id : 2, name : 'Grey Yordan', count : 1}
    ],

    reducers : {
        addCount(state, action){
            let index = state.findIndex((element)=>element.id==action.payload)
            state[index].count += 1
        },
        addCart(state, action){
            
            let found = state.findIndex((element)=>element.id == action.payload.id);
            
            // 이미 상품이 있으면 수량 + 1
            if (found != -1) {
                state[found].count += 1
            }
            // 없으면 추가
            else {
                state.push({id : action.payload.id, name : action.payload.title, count : 1})
            }

        },
        deleteProduct(state, action){
            state.splice(action.payload, 1);
        }
    }
})


export let {addCount, addCart, deleteProduct} = product.actions  // 함수 export


export {user, product}